<template>
  <div>
        <el-form ref="addForm" :rules="rules" :model="dataForm" label-width="150px">
            <el-row>
                <el-col :span="22">
                    <el-form-item label="词条名称" prop="name">
                        <el-input type="text" v-model="dataForm.name" placeholder="请输入名称"></el-input>
                    </el-form-item>
                </el-col>
             </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="所属系统">
                        <el-checkbox-group v-model="dataForm.type">
                            <el-checkbox label="干部综合"></el-checkbox>
                            <el-checkbox label="任免上会"></el-checkbox>
                            <el-checkbox label="干部监督"></el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>
                </el-col>
            </el-row>
           
            <div class="dialog-footer">
                <el-button type="primary" size="small" @click="saveArea()">保存</el-button>
                <el-button type="danger" size="small" @click="cancle()">取消</el-button>
            </div>
        </el-form>
    </div>
</template>

<script>
    export default{
        props: [
                'checkedData',
                'isEdit'//true：添加，false 编辑
        ],
        data(){
            return{
                //表单验证
                rules: {
                    name: [{
                        required: true,
                        message: '请输入词条名称',
                        trigger: 'change'
                    }],
                },
                dataForm:{
                    name: "",
                    type:[],
                },
               
            }
        },
        created(){ 
            this.setFormData()
        },
        methods:{
            setFormData(){
                if(this.isEdit === false){
                    this.dataForm.name = this.checkedData.name
                    this.dataForm.id = this.checkedData.id
                }else{
                    if(this.checkedData === ''){
                        this.dataForm.pId = 0 
                    }else{
                        this.dataForm.pId = this.checkedData.id
                    }
                }
            },
            //添加保存
            saveArea(){
                this.$refs['addForm'].validate((valid) => {
                    if(valid) {
                        this.$http({
                            url: this.$http.adornUrl(this.API.saveOrUpdateNameEntryWord),
                            method: 'post',
                            data: this.$http.adornData(
                                this.dataForm
                            )
                        }).then((res) =>{
                            var resData = res.data;
                            if(resData.code == 0) {
                                this.$message({
                                    message: '成功',
                                    type: 'success'
                                });
                                this.$emit("addback",true);
                            } else {
                                this.$message.error(resData.msg);
                            }
                        })
                    } 
                })
            },
            //取消
            cancle() {
                this.$emit("addback",false);
            } 
        }
    }
</script>

<style>
</style>



