<template>
    <div class="metaData">
        <el-container style="height: 100%;">
            <el-aside width="274px" >
                <div class="table-header-btn">
                    <div class="table-header-btn-l">
                        <el-button type="primary" size="small" @click="addClick()">添加</el-button>
                        <el-button type="primary" size="small" @click="edikClick()">编辑</el-button>
                        <el-button type="danger" size="small" @click="deleteClick()" >删除</el-button>
                        <el-button type="primary" size="small" @click="sortClick()" >排序</el-button>
                    </div>
                </div>
                <el-scrollbar :style="Height">
                    <metadataTree ref="moduleTree" :treeData="treeData" @node-click="orgTreeClick" style="margin-top:10px;"></metadataTree>
                </el-scrollbar>
            </el-aside>
            <el-main v-show="isBtn">
                <div class="table-header-btn">
                    <div class="table-header-btn-l"></div>
                    <div class="table-header-btn-r">
                        <el-button type="primary" size="small" @click="addListClick()">添加</el-button>
                        <el-button type="primary" size="small" @click="moveDirection('up')">上移</el-button>
                        <el-button type="primary" size="small" @click="moveDirection('down')">下移</el-button>
                        <el-button type="primary" size="small"  @click="saveMetaSort" >保存排序</el-button>
                    </div>
                </div>
                <el-table
                    v-loading="tableDataLoading"
                    ref="multipleTable"
                    :data="tableData"
                    @selection-change="selectionChangeHandle"
                >
                    <el-table-column type="selection" width="50" align="center"></el-table-column>
                    <el-table-column width="50" fixed type="index" :index="indexMethod" label="序号" align="center"></el-table-column>
                    <el-table-column prop="conditionDesc" label="描述" ></el-table-column>
                    <el-table-column label="是否启用">
                        <template slot-scope="scope">
                            <el-tag v-if="scope.row.status == 1" size="small">是</el-tag>
                            <el-tag v-else size="small" type="danger">否</el-tag>
                        </template>
                    </el-table-column>
                    <el-table-column label="操作" width="200">
                        <template slot-scope="scope">
                            <el-button type="primary" size="mini" plain @click="edikListClick(scope.$index,scope.row)">编辑</el-button>
                            <el-button type="danger" size="mini" plain @click="deleteListClick(scope.$index,scope.row)" >删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-main>
        </el-container>
        <!-- 左侧树、新增词条 -->
        <el-dialog
            :title="title"
             width="50%"
            :visible.sync="addEntyTitleVisable"
            v-if="addEntyTitleVisable"
            :close-on-click-modal ="false"
            :modal-append-to-body='false'
            append-to-body
        >
            <addEntyTitle :checkedData="checkedData" :isEdit="isEdit" @addback="addback"></addEntyTitle>
        </el-dialog>
        <!-- 左侧树、排序 -->
        <el-dialog
            title="词条排序"
            :visible.sync="sortNameVisable"
            width="50%"
            v-if="sortNameVisable"
            @close='closeDialog'
        >
            <sort-name :checkedData="checkedData"  @addback="addback"></sort-name>
        </el-dialog>
        <!-- 右侧列表、新增-->
        <el-dialog
            :title="title"
            :visible.sync="addListVisable"
            width="80%"
            v-if="addListVisable"
            :close-on-click-modal ="false"
        >
            <add-cont-dialog :checkedData="checkedData" ref="addContDialog" @addback="addback"></add-cont-dialog>
        </el-dialog>

    </div>
</template>
<script>
import {swapItems} from '@/utils';
import metadataTree from "@/components/baseComponents/orgTree.vue";
import addEntyTitle from "./add-enty-title.vue";
import sortName from "./sort-name.vue";
import addContDialog from "./add-cont-dialog.vue";
export default {
    name: "entry-management-index",
    components: {
        metadataTree,
        addEntyTitle,
        sortName,
        addContDialog,
    },
    data() {
        return {
            Height: {
                height: document.documentElement.clientHeight - 135 + "px"
            },
            tableData:[],
            treeData:[],
            isEdit: '', //true 添加，false：修改
            addEntyTitleVisable:false,
            sortNameVisable:false,
            addListVisable:false,
            tableDataLoading:false,
            currentRow:'',//选中的树数据
            isParent:'',//树结构数据、true是一级、false是二级
            isBtn: false,//右侧列表显示、隐藏
            tableDataSelections:[],
        }
    },
    created(){
       this.getDataTree();
    },
    methods: {
        //获取机构
        getDataTree(){
            this.$http({
                url: this.$http.adornUrl(this.API.getEntryTreeWord),
                method: 'post',
                data: this.$http.adornData({})
            }).then(({data}) => {
                if(data && data.code===0){
                    this.treeData=data.data;
                    // this.orgTreeClick(this.treeData[0])
                }else{
                    this.$message.error(data.msg);
                    this.treeData=[];
                }
            });
        },
        //机构树点击事件
        orgTreeClick(data) {
            this.currentRow = data;
            this.isParent = data.isParent;
            if(this.isParent === false){
                this.getDataList();
                this.isBtn = true;
            }else if(this.isParent === true){
                this.isBtn = false;
            }
        },
        //点击系统获取数据
        getDataList(){
            this.tableDataLoading = true;
            this.$http({
                url: this.$http.adornUrl(this.API.getEntryInformation),
                method: 'post',
                data: this.$http.adornData({
                    id:this.currentRow.id,
                })
            }).then(({data}) => {
                if(data && data.code===0){
                    this.tableData=data.data;
                }else{
                    this.$message.error(data.msg);
                }
                this.tableDataLoading = false;
            });
        },

        //左侧树、新增词条
        addClick(){
            if(this.isParent === false){
                this.$confirm('该目录下不允许添加！', '提示', {
                    confirmButtonText: '确定',
                    showCancelButton: false,
                    type: 'warning',
                    center: true
                });
                return;
            }else{
                this.checkedData = this.currentRow;
                this.addEntyTitleVisable = true;
                this.title ="新增词条";
                this.isEdit =true;
            }
        },
        //左侧树、编辑词条
        edikClick(){
            if(this.currentRow === null || this.currentRow === "") {
                this.$confirm('请选择一条数据！', '提示', {
                    confirmButtonText: '确定',
                    showCancelButton: false,
                    type: 'warning',
                    center: true
                });
                return;
            }
            this.checkedData = this.currentRow;
            this.addEntyTitleVisable = true;
            this.title ="词条编辑";
            this.isEdit = false;
        },

        //左侧树、词条目录排序
        sortClick(){
            this.checkedData = this.currentRow;
            this.sortNameVisable = true;
        },

        //左侧树、删除词条目录
        deleteClick(){
            if(this.currentRow === null || this.currentRow === "") {
                this.$confirm('请选择一条数据！', '提示', {
                    confirmButtonText: '确定',
                    showCancelButton: false,
                    type: 'warning',
                    center: true
                });
                return;
            }
            this.$confirm('确定删除选中记录?', '操作提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$http({
                    url: this.$http.adornUrl(this.API.deleteNameEntryWord),
                    method: 'post',
                    data: this.$http.adornData({
                        id:this.currentRow.id
                    })
                }).then(({data}) =>{
                    if(data.code === 0) {
                        this.getDataTree();
                        this.$message.success('删除成功');
                    } else {
                        this.$message.error(data.msg);
                    }
                })
            }).catch(() => {

            });
        },

        //右侧列表添加
        addListClick(){
            this.checkedData = this.currentRow;
            this.title = '信息项添加';
            this.addListVisable = true;
            this.isEdit = true;
        },
        //右侧列表编辑
        edikListClick(index,row){
            this.checkedData = this.currentRow;
            this.title = '信息项修改';
            this.addListVisable = true;
            this.isEdit = false;
            this.$nextTick(() => {
               this.$refs.addContDialog.getDataList(row);
            })
        },
        //右侧列表删除
        deleteListClick(index,row){
            this.$confirm('确定删除选中记录?', '操作提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$http({
                    url: this.$http.adornUrl(this.API.deleteInformation),
                    method: 'post',
                    data: this.$http.adornData({
                        id:row.id
                    })
                }).then(({data}) =>{
                    if(data.code === 0) {
                        this.getDataList();
                        this.$message.success('删除成功');
                    } else {
                        this.$message.error(data.msg);
                    }
                })
            }).catch(() => {

            });
        },

        selectionChangeHandle(val) {//多选
            this.tableDataSelections = val;
        },
        moveDirection(direction){ //移动
            if(this.tableDataSelections.length === 0) {
                this.$confirm('请选择一条数据！', '提示', {
                    confirmButtonText: '确定',
                    showCancelButton: false,
                    type: 'warning',
                    center: true
                });
                return;
            };
            var that=this,index;
            that.tableDataSelections.sort((prev,next)=>{
                return that.tableData.indexOf(prev)-that.tableData.indexOf(next)
            })
            if(direction=='down') that.tableDataSelections.reverse()
            that.tableDataSelections.forEach(item=>{
                index=that.tableData.indexOf(item); //得到右侧选中值的标识坐标
                if(direction=='up') that.moveUp(index);
                else if(direction=='down') that.moveDown(index)
            })
        },
        moveUp(index){//上移
            if(index==0) return;
            this.tableData=swapItems(this.tableData,index,index-1,'up');
        },
        moveDown(index){ //下移
            if(index==this.tableData.length-1) return;
            this.tableData=swapItems(this.tableData,index,index+1,'down');
        },
        saveMetaSort(){//保存排序
            var sortArr=[];
            if(this.tableData.length<0) return;
            this.tableData.forEach((item,index)=>{
                sortArr.push({
                    id:item.id,
                    orderno:index+1
                })
            })
            this.$http({
                url: this.$http.adornUrl(this.API.entrySortInformation),
                method: 'post',
                data: this.$http.adornData({
                    data:JSON.stringify(sortArr)
                })
            }).then(({data}) => {
                if(data && data.code===0){
                    this.getDataList()
                    this.$message.success('操作成功');
                }else{
                    this.$message.error(data.msg);
                }
            });
        },
        indexMethod(index) {
            return index + 1;
        },

        //机构排序关闭回调
        closeDialog() {
           this.getDataTree()
        },
        // 添加或修改组件回调方法--子组件回调数据的方法
	    // @param {Boolean} isRefresh是否需要刷新数据 true=刷新；false=不刷新
        addback(isRefresh){
            this.addEntyTitleVisable = false;
            this.addListVisable = false;
            if(isRefresh){
                this.getDataTree();
                this.getDataList();
            }
        },
    },
}
</script>
<style lang="scss" scoped>
    .el-aside {
        border-right: 1px solid #dcdfe6;
        padding-top: 10px;
    }
    .el-main {
        padding:0;
    }
    .el-form-item {
         margin-bottom: 0px;
    }
</style>



